@import '~node_modules/modern-normalize/modern-normalize.css';

body {
  font-family: 'Cera Pro', sans-serif;
  font-size: 16px;
  background-color: $background-color;
  color: $main-color;
}

a {
  color: $accent-color;
}

.section {
  padding-top: 40px;

  @include miw(768px) {
    padding-top: 60px;
  }

  @include miw(1440px) {
    padding-top: 120px;
  }
}

.hero-title {
  color: $accent-color;
  @include font(700, 48, 60);
}

.section-title {
  color: $accent-color;
  @include font(700, 36, 45);
}

.secondary-title {
  color: $accent-color;
  @include font(300, 18, 23);
}
